"use client";

import {
  createCategory,
  deleteCategory,
  getCategories,
  updateCategory,
} from "@/services/category.service";
import { useEffect, useState } from "react";
import AdminLayout from "@/components/admin/AdminLayout";

export default function AdminCategories() {
  const [categories, setCategories] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [name, setName] = useState("");
  const [type, setType] = useState("genre");
  const [editingId, setEditingId] = useState<number | null>(null);

  const fetchCategories = async () => {
    setLoading(true);
    try {
      const res = await getCategories();

      // ÿ•ÿ∞ÿß ŸÉÿßŸÜ res.data.data ŸáŸà ÿßŸÑŸÖÿµŸÅŸàŸÅÿ© ÿßŸÑÿ≠ŸÇŸäŸÇŸäÿ©
      const categoriesArray = Array.isArray(res.data)
        ? res.data
        : res.data.data;

      setCategories(categoriesArray || []);
    } catch (err) {
      console.error(err);
      setCategories([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  const handleCreateOrUpdate = async () => {
    if (editingId) {
      // ÿ™ÿπÿØŸäŸÑ
      await updateCategory(editingId, { name, type });
      setEditingId(null);
    } else {
      // ÿ•ŸÜÿ¥ÿßÿ°
      await createCategory({ name, type });
    }
    setName("");
    setType("genre");
    fetchCategories();
  };

  const handleEdit = (cat: any) => {
    setName(cat.name);
    setType(cat.type);
    setEditingId(cat.id);
  };

  const handleDelete = async (id: number) => {
    if (!confirm("ŸáŸÑ ÿ£ŸÜÿ™ ŸÖÿ™ÿ£ŸÉÿØ ŸÖŸÜ ÿßŸÑÿ≠ÿ∞ŸÅÿü")) return;
    await deleteCategory(id);
    fetchCategories();
  };

  return (
    <AdminLayout>
      <div className="p-6">
        <h2 className="text-2xl font-bold mb-4">ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿ™ÿµŸÜŸäŸÅÿßÿ™</h2>

        {/* ÿ•ÿ∂ÿßŸÅÿ© / ÿ™ÿπÿØŸäŸÑ */}
        <div className="flex gap-2 mb-6">
          <input
            placeholder="ÿßÿ≥ŸÖ ÿßŸÑÿ™ÿµŸÜŸäŸÅ"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="p-2 rounded bg-gray-700 text-white"
          />
          <select
            value={type}
            onChange={(e) => setType(e.target.value)}
            className="p-2 rounded bg-gray-700 text-white"
          >
            <option value="genre">ŸÜŸàÿπ</option>
            <option value="topic">ŸÖŸàÿ∂Ÿàÿπ</option>
            <option value="age">ÿßŸÑŸÅÿ¶ÿ© ÿßŸÑÿπŸÖÿ±Ÿäÿ©</option>
          </select>
          <button
            onClick={handleCreateOrUpdate}
            className="bg-red-600 px-4 rounded text-white"
          >
            {editingId ? "ÿ™ÿπÿØŸäŸÑ" : "ÿ•ÿ∂ÿßŸÅÿ©"}
          </button>
          {editingId && (
            <button
              onClick={() => {
                setEditingId(null);
                setName("");
                setType("genre");
              }}
              className="bg-gray-600 px-4 rounded text-white"
            >
              ÿ•ŸÑÿ∫ÿßÿ°
            </button>
          )}
        </div>

        {/* ÿ¨ÿØŸàŸÑ ÿßŸÑÿ™ÿµŸÜŸäŸÅÿßÿ™ */}
        {loading ? (
          <p>ÿ¨ÿßÿ±Ÿç ÿßŸÑÿ™ÿ≠ŸÖŸäŸÑ...</p>
        ) : (
          <table className="min-w-full bg-gray-800 text-white rounded-lg ">
            <thead className="text-right">
              <tr>
                <th className="p-2">#</th>
                <th className="p-2">ÿßŸÑÿßÿ≥ŸÖ</th>
                <th className="p-2">ÿßŸÑŸÜŸàÿπ</th>
                <th className="p-2">ÿßŸÑÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
              </tr>
            </thead>
            <tbody>
              {categories.map((cat, idx) => (
                <tr key={cat.id} className="border-t border-gray-700">
                  <td className="p-2">{idx + 1}</td>
                  <td className="p-2">{cat.name}</td>
                  <td className="p-2">{cat.type}</td>
                  <td className="p-2 flex gap-2">
                    <button
                      onClick={() => handleEdit(cat)}
                      className="bg-yellow-500 px-2 rounded"
                    >
                      ÿ™ÿπÿØŸäŸÑ
                    </button>
                    <button
                      onClick={() => handleDelete(cat.id)}
                      className="bg-red-600 px-2 rounded"
                    >
                      ÿ≠ÿ∞ŸÅ
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </AdminLayout>
  );
}
